	module.exports = {"data":{"markdownRemark":{"html":"<h2>Area of a Parallelogram</h2>\n<p>A parallelogram is a four-sided figure in which the opposite sides and angles are equal. It can be considered as being made up of two same  triangles sticked base to base.\n<img src=\"http://www.onemathematicalcat.org/Math/Geometry_obj/graphics/area_parallelogram.bmp\" alt=\"alt text\"></p>\n<p>The area of a triangle is <code>1/2 * base * height</code> and the area of a parallelogram is the addition of areas of the two same triangles.\nTherefore,</p>\n<h3>Example</h3>\n<p>Consider a paralleogram as shown in the figure.\n<img src=\"http://www.proprofs.com/quiz-school/upload/yuiupload/812246308.jpg\" alt=\"alt text\"></p>\n<p>Therefore, Area = 6 * 2.5 = 15 (m)^2</p>\n<h4>More Information:</h4>\n<p>For more details visit <a href=\"https://en.wikipedia.org/wiki/Parallelogram\">wiki</a></p>","fields":{"slug":"/mathematics/area-of-a-parallelogram/"},"frontmatter":{"title":"Area of a Parallelogram"}}},"pathContext":{"slug":"/mathematics/area-of-a-parallelogram/"}}

